@charset "UTF-8";

// —————————————————————————————————————————————————————————————————
// SCROLL ANIM
// —————————————————————————————————————————————————————————————————

.slideanim {
    visibility:hidden;
    visibility:visible\9;
    // For old IE browsers IE6-8

    &.slide {
    // class added on scroll event
        visibility: visible; 
        animation: slide 1s;
    }
}